.container{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.inner{
    border: 1px solid rgba(0,0,0,0.1);
    cursor: initial;
    background-color: white;
}
.row{
    display: flex;
}
.brt, .brc, .brd, .brp{
    border-right: 1px solid rgba(0,0,0,0.1);
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.brd{
    border-bottom: 3px solid #ff3131;
}
.brt:last-child, .brc:last-child, .brd:last-child, .brp:last-child{
    border-right: none;
}
.brf, .brs, .brt{
    border-top: 1px solid rgba(0,0,0,0.1);
    border-right: 1px solid rgba(0,0,0,0.1);
}
.brf:last-child, .brs:last-child, .brt:last-child, .brl:last-child{
    border-right: none;
}
.brl{
    border-right: 1px solid rgba(0,0,0,0.1);
    border-top: 3px solid #FFC000;
}
.title{
    width: 60px;
    display: flex;
    line-height: 60px;
    padding: 1rem 0rem;
    border-top: none;
}
.title h4{
    writing-mode: vertical-rl;
    transform: rotate(180deg);
}
.carry{
    width: 60px;
    height: 40px;
    line-height: 40px;
    text-align: center;
}
.c1{
    color: #9DC3E6;
}
.c2{
    color: #C5E0B4;
}
.c3{
    color: #FFCCFF;
}
.digits{
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
}
.digits h1{
    font-size: 2rem;
    font-weight: 900;
    color: #ff3131;
}
input{
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font-size: 2.5rem;
    font-weight: 900;
    padding-left: 10px;
    color: #7F7F7F;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
.results{
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-size: 1.2rem;
    font-weight: 900;
    position: relative;
}
.r1{
    background-color: #FFCCFF;
    color: #7030A0;
}
.r2{
    background-color: #E2F0D9;
    color: #00B050;
}
.r3{
    background-color: #DEEBF7;
    color: #0070C0;
}
.r4{
    background-color: #ffffff;
    color: #FFC000;
}
.frCarry{
    position: absolute;
    top: 0px;
    left: 5px;
    width: 10px;
    height: 10px;
    line-height: 20px;
    color: black;
}