.container{
    width: 100%;
    height: 100vh;
    background-color: #e3e3e3;
}
.inner{
    width: 100%;
    height: calc(100vh - 110px);
    padding: 3vw;
    padding-right: calc(3vw + 60px);
    display: flex;
    gap: 3vw;
    overflow: hidden;
}
.board_container{
    width: 362px;
    overflow-x: hidden;
    overflow-y: auto;
    flex-shrink: 0;
    background-color: #e0e0e0;
    box-shadow:  20px 20px 40px #bebebe,
                 -20px -20px 40px #ffffff;    
}
.number_pad_container{
    position: fixed;
    top: 50%;
    right: -352px;
    width: 412px;
    padding-right: 1rem;
    height: 50vh;
    transform: translateY(-50%);
    display: flex;
    background-color: #e0e0e0;
    border: 1px solid rgba(0,0,0,0.1);
}
.keypadTrigger{
    width: 60px;
    flex-shrink: 0;
    height: 100%;
}
.keypadTrigger button{
    width: 100%;
    height: 100%;
    font-size: 2rem;
    color: var(--p);
    cursor: pointer;
    border: none;
    outline: none;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-top-right-radius: 0rem;
    border-bottom-right-radius: 0rem;
    background-color: #e0e0e0;
    box-shadow:  6px 6px 12px #bebebe,
                 -6px -6px 12px #ffffff;
}
.keypadTrigger button:hover{
    color: var(--p-dark);
}
.number_pad_container_grow{
    animation: grow 0.5s ease-in-out 1 forwards;
    overflow: hidden;
}
.number_pad_container_grow .keypadTrigger button{
    border-top-left-radius: 0rem;
    border-bottom-left-radius: 0rem;
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
}
.number_pad_container_shrink{
    animation: shrink 0.5s ease-in-out 1 forwards;
}
.number_pad_container_shrink .keypadTrigger button{
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-top-right-radius: 0rem;
    border-bottom-right-radius: 0rem;
}
@keyframes grow {
    from{
        right: -352px;
        z-index: 0;
    }
    to{
        right: 0px;
        z-index: 1;
    }
}
@keyframes shrink {
    from{
        right: 0px;
        z-index: 1;
    }
    to{
        right: -352px;
        z-index: 0;
    }
}
.explainer_container{
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    background-color: #e0e0e0;
    box-shadow:  20px 20px 40px #bebebe,
                 -20px -20px 40px #ffffff;        
}
@media screen and (max-width:824px) {
    .inner{
        height: calc(100vh - 110px - 3vw);
        padding-right: 3vw;
        flex-direction: column;
    }
    .board_container{
        border-radius: 1rem;
    }
    .board_container, .explainer_container{
        width: 100%;
        height: 50%;
        box-shadow: none;
        border: 1px solid rgba(0,0,0,0.1);
    }
    .number_pad_container{
        width: 100%;
        box-shadow: none;
        top: calc(50vh + 60px);
        right: calc(60px - 100%);
        height: calc(50vh - 60px);
        transform: translateY(0);
        border-radius: 0;
        background-color: transparent;
    }
    .number_pad_container_grow{
        background-color: #e0e0e0;
    }
    .number_pad_container_shrink{
        background-color: transparent;
    }
    .keypadTrigger{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .keypadTrigger button{
        width: 60px;
        height: 60px;
        background-color: white;
        box-shadow: none;
    }
    @keyframes grow {
        from{
            right: calc(60px - 100%);
            z-index: 0;
        }
        to{
            right: 0;
            z-index: 1;
        }
    }
    @keyframes shrink {
        from{
            right: 0;
            z-index: 1;
        }
        to{
            right: calc(60px - 100%);
            z-index: 0;
        }
    }
}
@media screen and (max-width:524px){
    .inner{
        padding: 0rem;
        padding-bottom: 3vw;
    }
    .board_container{
        border-top: none;
        border-top-left-radius: 0rem;
        border-top-right-radius: 0rem;
    }
}