.container{
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5vw 0rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
}
.close{
    margin-bottom: 2rem;
    display: flex;
    width: 100%;
    justify-content: flex-end;
}
.close button{
    padding: 0.5rem 1rem;
    background-color: transparent;
    color: var(--s-dark);
    border: 1px solid var(--s-dark);
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 5px;
}
.close button:hover{
    background-color: var(--s-dark);
    color: white;
    border-color: transparent;
}
.inner{
    background-color: white;
    padding: 2rem;
    border-radius: 1rem;
    transform: scale(0);
    animation: zoom 0.5s ease-in-out 1 forwards;
}

@keyframes zoom {
    from{
        transform: scale(0);
    }
    to{
        transform: scale(1);
    }
}