.btn button{
    width: 200px;
    height: 45px;
    cursor: pointer;
    outline: none;
    font-size: 1.2rem;
    border: 2px solid var(--p-light);
    background-color: transparent;
    font-weight: 900;
    position: relative;
    display: block;
    text-align: center;
    overflow: hidden;
    text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
    -webkit-transition: all 0.2s ease;
    -moz-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
    color: white;
    margin: 0;
    transform: scale(1);
    background-color: var(--p-light);
    border-radius: 0.5rem;
    user-select: none;
}
.btn button::after{
    content: "";
    position: absolute;
    height: 0%;
    left: 50%;
    top: 50%;
    width: 150%;
    z-index: -1;
    -webkit-transition: all 0.4s ease 0s;
    -moz-transition: all 0.4s ease 0s;
    -o-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s;
    opacity: 0;
    background-image: -webkit-linear-gradient( transparent 50%, rgba(179, 101, 101, 0.3) 50%);
    background-image: -moz-linear-gradient(transparent 50%, rgba(179, 101, 101, 0.3) 50%);
    background-image: linear-gradient(transparent 50%, rgba(179, 101, 101, 0.3) 50%);
    background-size: 10px 10px;
    -moz-transform: translateX(-50%) translateY(-50%) rotate(-25deg);
    -ms-transform: translateX(-50%) translateY(-50%) rotate(-25deg);
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(-25deg);
    transform: translateX(-50%) translateY(-50%) rotate(-25deg);
}
.btn button:hover::after{
    height: 600% !important;
    opacity: 1;
}
.btn button:active{
    -moz-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
}
.btn button i{
    display: none;
}
@media screen and (max-width:900px) {
    .btn button{
        width: 45px;
    }
    .btn button span{
        display: none;
    }
    .btn button i{
        display: block;
    }
}